<template>
  <ul class="catalog__pagination pagination">
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow"
         :class="{'pagination__link--disabled': page === 1}" aria-label="Предыдущая страница"
         @click.prevent="paginatePrev()" @keyup.enter="paginatePrev()">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-left"></use>
          </svg>
        </a>
      </li>
      <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
        <a class="pagination__link" :class="{'pagination__link--current': pageNumber === page}"
         @click.prevent="paginate(pageNumber)" @keyup.enter="paginate(pageNumber)">
          {{ pageNumber }}
        </a>
      </li>
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow" href="#"
        :class="{'pagination__link--disabled': page === Math.max(pages)}"
         aria-label="Следующая страница"
         @click.prevent="paginateNext()" @keyup.enter="paginateNext()">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </a>
      </li>
    </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    paginateNext() {
      if (this.page < this.pages) {
        this.$emit('paginate', this.page + 1);
      }
    },
    paginatePrev() {
      if (this.page > 1) {
        this.$emit('paginate', this.page - 1);
      }
    },
  },
};
</script>
